<template>
    <div @click="menu=''" class="def_home" :style="margin_style()">
        <div class="onpage">
            <h1>View/Search Clients (<b>{{Arrusers().length}}</b>)</h1>
            <div class="searchUsers">
                <div class="usersContent">
                    <div class="img"><i class="fas fa-search"></i></div>
                    <div class="inputdiv">
                        <span class="des">Username</span>
                        <input v-model="name" type="text">
                    </div>
                    <div class="inputdiv">
                        <span class="des">Email</span>
                        <input v-model="mail" type="text">
                    </div>
                    <div class="inputdiv">
                        <span class="des">Status</span>
                        <select name="status">
                            <option value="Any">Any</option>
                            <option value="Active">Active</option>
                            <option value="Suspension">Suspension</option>
                        </select>
                    </div>
                    <div @click="searchGo()" class="go"><input type="button" value="Search"></div>
                </div>
            </div>
            <div class="pageContent">
                <div class="user" v-for="user in Arrusers()" :key="user.userid">
                    <div class="usermenu" v-on:click.stop.prevent>
                        <i v-on:click.stop.prevent="menu = menu == user.userid ? '':user.userid"
                            class="fas fa-ellipsis-h"></i>
                        <div v-if="user.userid == menu" class="ul">
                            <li>تعديل الاكونت <i class="fa fa-user "></i></li>
                            <li>أيقاف الاكونت <i class="fa fa-ban"></i></li>
                            <li>حذف الاكونت <i class="far fa-trash-alt"></i></li>
                        </div>
                    </div>
                    <div class="userbox">
                        <div class="imguser"><img :src="img(user.username, user.icon, user.sex)"> </div>
                        <div class="username"><span>{{user.username}}</span></div>
                    </div>
                    <div class="time"><time>{{timestamp(user.insertDate)}}</time></div>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
    export default {

        created() {
            this.$store.getters.API(`get.php?action=inc_users`, data => {
                if (data) {
                    this.$store.state.modal_Container = false;
                    this.users = data;
                }
            });
        },

        data() {
            return {
                users: [],
                menu: "",
                name: "",
                mail: "",
                search: []
            }
        },
        watch: {
            name: function (val) {
                this.name = val
                this.mail = "";
                this.searchGo()

            },
            mail: function (val) {
                this.mail = val;
                this.name = "";
                this.searchGo()
            }
        },
        methods: {
            Arrusers() {
                var s = this.search;
                return s.length > 0 ? s : this.users;
            },
            searchGo() {
                this.search = [];
                this.users.forEach(element => {
                    var txt = this.mail ? {
                        c: element.mails,
                        m: this.mail
                    } : {
                        c: element.username,
                        m: this.name
                    };
                    if (txt.c.toUpperCase().indexOf(txt.m.toUpperCase()) > -1) {
                        this.search.push(element)
                    }
                });
            },
            timestamp(times) {

                var TimeAgo = (function () {
                    var self = {};

                    self.locales = {
                        prefix: '',
                        sufix: 'منذ',
                        seconds: 'دقيقه',
                        minute: 'دقيقة',
                        minutes: ' دقيقة %d',
                        hour: 'ساعه',
                        hours: '  ساعه %d',
                        day: 'يوم',
                        days: ' يوم %d',
                        month: 'شهر',
                        months: ' أشهر %d',
                        year: 'عام',
                        years: ' أعوام %d'
                    };

                    self.inWords = function (timeAgo) {
                        var seconds = Math.floor((new Date() - parseInt(timeAgo)) / 1000),
                            separator = this.locales.separator || ' ',
                            words = this.locales.prefix + separator,
                            interval = 0,
                            intervals = {
                                year: seconds / 31536000,
                                month: seconds / 2592000,
                                day: seconds / 86400,
                                hour: seconds / 3600,
                                minute: seconds / 60
                            };

                        var distance = this.locales.seconds;

                        for (var key in intervals) {
                            interval = Math.floor(intervals[key]);

                            if (interval > 1) {
                                distance = this.locales[key + 's'];
                                break;
                            } else if (interval === 1) {
                                distance = this.locales[key];
                                break;
                            }
                        }

                        distance = distance.replace(/%d/i, interval);
                        words += this.locales.sufix + separator + distance;

                        return words.trim();
                    };

                    return self;
                }());


                return TimeAgo.inWords(times);

            },
            margin_style() {
                return this.$store.getters.margin_style;
            },
            img(n, i, s) {
                //  var url = 'https://www.7kawichat.com/chat/vue/profile/'
                var url = 'https://localhost/7kawi/chat/vue/profile/'
                if (/https/.test(i)) {
                    return i;
                }

                if (i && i != "0") {

                    return url + 'index.php?url=' + i;


                } else {

                    return `${url}dev.php?name=` + s;


                }


            },
        }
    }
</script>
<style>
    .user .usermenu .ul {
        position: absolute;
        right: 5px;
        background: white;
        width: 190px;
        height: 70px;
        padding: 5px;
        animation: conditionalOpen 0.5s normal forwards ease-in-out;
        z-index: 9999
    }

    .user .usermenu {
        position: relative
    }

    .user .ul li:hover {
        background: #f5f5f5;
        color: red
    }

    .user li {
        list-style: none;
        margin: 3px 0;
        font-size: 13px;
        font-family: "Almarai" !important;
        ;
        color: mediumblue;
        font-weight: 500;
        padding: 1px 0;
    }

    .user li i {
        list-style: none;
        margin-left: 5px
    }

    .pageContent .user .time {
        font-size: 14px;
        color: #666;
        margin-top: 10px;
        margin-bottom: 3px;
        float: right;
        margin-right: 4px;

    }

    .pageContent .user .time:after {
        content: ": أخر ظهور "

    }

    .pageContent .user .time time {
        margin: 0 5px;
    }

    .user .username {
        overflow: hidden;
        width: 100px;
    }

    .user {
        vertical-align: middle;
        height: 90px;
    }

    .pageContent .user {
        display: inline-block;
        margin: 10px;
        background: #f5f5f5;
        width: 200px;
        padding-left: 5px;
        box-shadow: 1px 1px 7px 1px #ddd
    }

    .pageContent .user img {
        width: 40px;
        height: 40px;
    }

    .pageContent .user .imguser {
        display: inline-block;
        vertical-align: middle
    }

    .pageContent .user .username {
        display: inline-block;
        padding-left: 5px;
        color: #3c8dbc;
        font-weight: 600;
    }

    .pageContent .usermenu {
        text-align: right;
        padding-right: 10px;
        cursor: pointer;

    }

    .def_home .onpage {
        text-align: left;
        padding: 0 10px;

    }

    .searchUsers {
        background: #f6f6f6;
        padding: 10px;
        border-bottom: 1px dashed #ccc;
    }

    .searchUsers .usersContent div {
        display: inline-block;
        margin: 0 10px;
    }

    .searchUsers input {
        width: 100%;
        height: 34px;
        padding: 6px 12px;
        font-size: 14px;
        line-height: 1.42857143;
        color: #555;
        background-color: #fff;
        background-image: none;
        border: 1px solid #ccc;
    }

    .searchUsers .usersContent div span {
        display: block;
    }

    .searchUsers .usersContent .img {
        background: #71c43a;
        padding: 5px 10px;
        color: white;
        border-radius: 50%;
        font-size: 30px
    }

    .searchUsers .go input {
        background: #3c8dbc;
        color: white
    }
</style>